import React from 'react'
import HomeIcon from '@mui/icons-material/Home';
import VideoLibraryIcon from '@mui/icons-material/VideoLibrary';
import SubscriptionsIcon from '@mui/icons-material/Subscriptions';
import List from './List';
let ul1con=[
  {
    image:<HomeIcon/>,
    listcon:"Home",
    path:"/home"
  },
  {
    image:<VideoLibraryIcon/>,
    listcon:"Shorts",
    path:"/shorts"
  },
  {
    image:<SubscriptionsIcon/>,
    listcon:"Subscriptions",
    path:"/Subscriptions"
  }
]
const Ul1 = () => {
  return (
    <ul style={{listStyle:"none",display:"flex",flexDirection:"column",width:"170px",height:"120px",justifyContent:"space-evenly",gap:"10px"}}>
      {
        ul1con.map(({image,listcon,path})=>{
          return <List image={image} listcon={listcon} path={path} key={listcon}/>
        })
      }
    </ul>
)
}

export default Ul1
