import React from 'react'
import PeopleAltIcon from '@mui/icons-material/PeopleAlt';
import RestoreIcon from '@mui/icons-material/Restore';
import PlaylistPlayIcon from '@mui/icons-material/PlaylistPlay';
import SmartDisplayIcon from '@mui/icons-material/SmartDisplay';
import WatchLaterIcon from '@mui/icons-material/WatchLater';
import ThumbUpOffAltIcon from '@mui/icons-material/ThumbUpOffAlt';
import FileDownloadIcon from '@mui/icons-material/FileDownload';
import ContentCutIcon from '@mui/icons-material/ContentCut';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import List from './List';
let ul2=[
    {
        image:<PeopleAltIcon/>,
        listcon:"Your Channel",
        path:"/yourchannel"
    },
    {
        image:<RestoreIcon/>,
        listcon:"History",
        path:"/history"
    },
    {
        image:<PlaylistPlayIcon/>,
        listcon:"Playlists",
        path:"/playlists"
    },
    {
        image:<SmartDisplayIcon/>,
        listcon:"Your videos",
        path:"/yourvideos"
    },
    {
        image:<WatchLaterIcon/>,
        listcon:"Watch later",
        path:"/watchlater"
    },
    {
        image:<ThumbUpOffAltIcon/>,
        listcon:"Liked videos",
        path:"/likedvideos"
    },
    {
        image:<FileDownloadIcon/>,
        listcon:"Downloads",
        path:"/downloads"
    },
    {
        image:<ContentCutIcon/>,
        listcon:"Your clips",
        path:"/yourclips"
    }
]
const Ul2 = () => {
  return (
    <>
    <span style={{display:"flex",marginLeft:"40px",fontSize:"20px",alignItems:"center",fontWeight:"bold"}}>You <KeyboardArrowRightIcon/></span>
    <ul style={{listStyle:"none",display:"flex",flexDirection:"column",width:"170px",height:"120px",justifyContent:"space-evenly",gap:"15px"}}>
        {
          ul2.map(({image,listcon,path})=>{
            return <List image={image} listcon={listcon} path={path} key={listcon}/>
          })  
        }
    </ul>
    </>
  )
}

export default Ul2