import React from 'react'
import Ul1 from './Ul1'
import Ul2 from './Ul2'
import Ul3 from './Ul3'


const NavBar = () => {
  return (
    <nav style={{width:"260px",height:"665px",overflowY:"scroll",overflowX:"hidden",marginTop:"-20px"}}>
        <Ul1/>
        <hr style={{width:"200px"}}/>
        <Ul2/>
        <hr style={{marginTop:"280px",width:"200px"}}/>
        <Ul3/>
    </nav>
  )
}

export default NavBar