import React from 'react'
import { Link } from 'react-router-dom'

const ListHome = ({bcon,path}) => {
  return (
    <>
   <Link  to={path}><button style={{height:"32px",fontSize:"18px",border:"none",borderRadius:"5px",cursor:"pointer"}}>{bcon}</button></Link>
    </>
  )
}

export default ListHome