import React from 'react'
import { Link } from 'react-router-dom'

const TrendsList = ({tpath,tcon}) => {
  return (
    <>
        <Link to={tpath} style={{textDecoration:"underline",color:"#000",fontSize:"18px",fontWeight:'500',cursor:'pointer',position:"sticky",top:"0"}}>{tcon}</Link>
    </>
  )
}

export default TrendsList