import React from 'react'
import { Link } from 'react-router-dom'

const List = ({image,listcon,path}) => {
  return (
    <>
    <Link className='list' style={{color:"#000",display:"flex",textDecoration:"none",gap:"30px",transition:"all 0.3s",paddingLeft:"15px",borderRadius:"20px",height:"30px",width:"200px",paddingTop:"6px",marginLeft:"-20px"}} to={path}><span>{image}</span><span>{listcon}</span></Link>
    </>
  )
}

export default List