import React from 'react'
import TrendsList from './TrendsList'

let tnav=[
    {
        tpath:"/now",
        tcon:"Now"
    },
    {
        tpath:"/musict",
        tcon:"Music"
    },
    {
        tpath:"/gaming",
        tcon:"gaming"
    },
    {
        tpath:"/tmovies",
        tcon:"Movies"
    }
]
const Trendsnav = () => {
  return (
    <ul style={{height:"50px",display:'flex',gap:"30px",position:"sticky",alignItems:"center",top:"0px",backgroundColor:"#fff",marginTop:"100px",right:'720px'}}>
        {
            tnav.map(({tpath,tcon})=>{
                return <TrendsList tpath={tpath} tcon={tcon}/>
            })
        }
    </ul>
  )
}

export default Trendsnav