import React, { useRef } from 'react'
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
const Videos = ({src,imgsrc,con,scon,min}) => {
 
  return (
    <div id='vediodiv' style={{width:"350px",display:"flex",flexDirection:"column",justifyContent:"center",alignItems:'center',height:"360px",cursor:'pointer',borderRadius:"10px",transition:"all 0.4s"}}>
          <div id='iframe' style={{height:'230px',display:"flex",justifyContent:'center',placeContent:"center",alignItems:'center'}}>{src }</div> 
        <div style={{width:"300px",height:"100px",display:'flex',flexDirection:"column"}}>
           <div id='image' style={{display:"flex",justifyContent:"center",alignContent:"center",alignItems:"center",marginTop:"-20px",height:"100px",marginRight:"40px"}}>
                <img style={{height:"35px",borderRadius:"50px"}} src={imgsrc} alt="error" />
                <h4>{con}</h4>
           </div>
           <div style={{marginTop:"-20px",marginLeft:"48px"}}>
           <span>{scon} <CheckCircleIcon style={{fontSize:"12px",color:"#ccc"}}/></span><br />
            <span>{min}</span>
           </div>
        </div>
    </div>
  )
}

export default Videos