import React from 'react'
import ListHome from './ListHome'
let buttond=[
    {
      bcon:"All",
      path:"/all"
    },
    {
      bcon:"Mixes",
      path:"/mixes"
    },
    {
      bcon:"Music",
      path:"/music"
    },
    {
      bcon:"Telugu cinema",
      path:"/telugu"
    },
    {
      bcon:"Trailers",
      path:"/trailers"
    },
    {
      bcon:"Web series",
      path:"/webseries"
    },
    {
      bcon:"Jabardasth",
      path:"/jabardasth"
    },
    {
      bcon:"Live",
      path:"/live"
    },
    {
      bcon:"Mukbang",
      path:"/mukbang"
    },
    {
      bcon:"Gaming",
      path:"/gaming"
    },
    {
      bcon:"Culinary arts",
      path:"/culinaryarts"
    }
  ]
const AllHomeNarBar = () => {
  return (
    <div style={{width:"81%",marginLeft:'264px',position:"fixed",height:"40px",bottom:"640px",display:"flex",justifyContent:"space-evenly",textAlign:"center"}}>
     {
        buttond.map(({bcon,path})=>{
            return <ListHome bcon={bcon} path={path} key={bcon}/>
        })
     }
      
    </div>
  )
}

export default AllHomeNarBar