import React from 'react'

import WhatshotIcon from '@mui/icons-material/Whatshot';
import WorkOutlineIcon from '@mui/icons-material/WorkOutline';
import MusicNoteIcon from '@mui/icons-material/MusicNote';
import MovieIcon from '@mui/icons-material/Movie';
import SensorsIcon from '@mui/icons-material/Sensors';
import SportsEsportsIcon from '@mui/icons-material/SportsEsports';
import ArticleIcon from '@mui/icons-material/Article';
import EmojiEventsIcon from '@mui/icons-material/EmojiEvents';
import EmojiObjectsIcon from '@mui/icons-material/EmojiObjects';
import DiamondIcon from '@mui/icons-material/Diamond';
import PodcastsIcon from '@mui/icons-material/Podcasts';
import List from './List';
let ul3=[
    {
        image:<WhatshotIcon/>,
        listcon:"Trending ",
        path:"/trending"
    },
    {
        image:<WorkOutlineIcon/>,
        listcon:"Shopping ",
        path:"/shopping"
    },
    {
        image:<MusicNoteIcon/>,
        listcon:"Music",
        path:"/music"
    },
    {
        image:<MovieIcon/>,
        listcon:"Movies",
        path:"/movies"
    },
    {
        image:<SensorsIcon/>,
        listcon:"Live",
        path:"/live"
    },
    {
        image:<SportsEsportsIcon/>,
        listcon:"Gaming",
        path:"/gaming"
    },
    {
        image:<ArticleIcon/>,
        listcon:"News",
        path:"/news"
    },
    {
        image:<EmojiEventsIcon/>,
        listcon:"Sports",
        path:"/sports"
    },
    {
        image:<EmojiObjectsIcon/>,
        listcon:"Courses",
        path:"/courses"
    },
    {
        image:<DiamondIcon/>,
        listcon:"Fashion & Beauty",
        path:"/fashion"
    },
    {
        image:<PodcastsIcon/>,
        listcon:"Podcasts",
        path:"/podcasts"
    }
]
const Ul3 = () => {

  return (
   <div >
   <span style={{fontSize:"16px",fontWeight:"bold",marginLeft:"40px"}}>Explore</span>
   <ul style={{listStyle:"none",display:"flex",flexDirection:"column",width:"170px",height:"120px",justifyContent:"space-evenly",gap:"15px"}}>
        {
            ul3.map(({image,listcon,path})=>{
                return <List image={image} listcon={listcon} path={path} key={listcon}/>
            })
        }
   </ul>
   </div>
  )
}

export default Ul3